.header-main {
  align-items: flex-end;
  background-color: $pale-cyan;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  max-width: 1024px;
  width: 100%;

  @media #{$desk-up} {
    flex: 0 0 $body-line-height * 8;
    flex-direction: row;
    max-height: $body-line-height * 8;
  }

  &__heading {
    align-items: flex-end;
    animation-duration: 208.333ms;
    animation-iteration-count: infinite;
    animation-name: jitter;
    animation-timing-function: step-start;
    background-color: $pale-indigo;
    background-image: url(data:image/png;base64,),
    repeating-linear-gradient(45deg, transparent 5px, $screen-green 5px, $screen-green 10px,
      $screen-indigo 10px, $screen-indigo 35px, $screen-cyan 35px, $screen-cyan 40px,
      $screen-green 40px, $screen-green 50px, $screen-white 50px, $screen-white 60px,
      $screen-cyan 60px, $screen-cyan 70px, $screen-red 70px, $screen-red 80px,
      $screen-yellow 80px, $screen-yellow 90px, $screen-cyan 90px, $screen-cyan 110px,
      $screen-indigo 110px, $screen-indigo 120px, $screen-green 120px, $screen-green 140px
      ),
    repeating-linear-gradient(135deg, transparent 5px, $screen-green 5px, $screen-green 10px,
      $screen-indigo 10px, $screen-indigo 35px, $screen-cyan 35px, $screen-cyan 40px,
      $screen-green 40px, $screen-green 50px, $screen-white 50px, $screen-white 60px,
      $screen-cyan 60px, $screen-cyan 70px, $screen-red 70px, $screen-red 80px,
      $screen-yellow 80px, $screen-yellow 90px, $screen-cyan 90px, $screen-cyan 110px,
      $screen-indigo 110px, $screen-indigo 140px, $screen-green 140px, $screen-green 160px
    );
    color: $magenta;
    display: flex;
    height: $body-line-height * 5;
    margin-top: 0;
    padding-left: $column-gutter / 2;
    padding-right: $column-gutter / 2;
    text-shadow: 3px 3px 0 $yellow;
    width: 100%;

    @media #{$desk-up} {
      flex: 0 0 33.333%;
      height: $body-line-height * 8;
      max-width: 33.333%;
    }
  }

  @keyframes jitter {
    0% {
      background-position: -3% 5%;
      // background-size: 120% 100%;
      // color: $magenta;
    }

    20% {
      background-position: -13% -25%;
      // background-size: 100% 100%;
      // color: $indigo;
    }

    40% {
      background-position: 10% 5%;
      // background-size: 600% 101%;
      // color: $cyan;
    }

    60% {
      background-position: 20% -35%;
      // background-size: 100% 100%;
      // color: $green;
    }

    80% {
      background-position: -17% 53%;
      // background-size: 160% 101%;
      // color: $red;
    }
  }

  &__nav {
    flex: 0 0 66.666%;
    font-size: rem-calc(14);
    line-height: $body-line-height * 2;
    max-width: 66.666%;
    padding-right: $column-gutter / 2;
    text-align: right;
  }

  &__nav-link {
    @extend %link;

    margin-left: .5rem;
  }
}



